*{
  margin: 0px;
  padding: 0px;
}
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: white;
}

::-ms-input-placeholder { /* Microsoft Edge */
  color: white;
}
.flex{
  display: flex;
}
.flex-col {
  flex-direction: column;
}
.flex-row {
  flex-direction: row;
}
.justify-center{
  justify-content: center;
}
.content-center {
  align-content: center;
}
.bg-black {
  background-color: rgb(0 0 0 );
}
.min-h-screen {
  min-height: 100vh;
}
.max-h-fit {
  max-height: fit-content;
}
.font-mono {
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}
.bg-gray-400 {
  --tw-bg-opacity: 1;
  background-color: rgb(156 163 175 / var(--tw-bg-opacity));
}
.mt-20 {
  margin-top: 80px;
}
.rounded {
  border-radius: 4px;
}
.bg-opacity-30 {
  --tw-bg-opacity: 0.3;
}
.w-350 {
  width: 350px;
}
.h-fit {
  height: fit-content;
}
.border-gray-500 {
  --tw-border-opacity: 1;
  border-color: rgb(107 114 128 / var(--tw-border-opacity));
}
.text-white {
  color: rgb(255 255 255);
}
.m-auto {
  margin: auto;
}
.text-3xl {
  font-size: 30px;
  line-height: 36px;
}
.mt-3 {
  margin-top: 12px;
}
.font-extrabold {
  font-weight: 800;
}
.mt-4 {
  margin-top: 16px;
}
.p-3 {
  padding: 4px;
}
.h-11 {
  height: 44px;
}
.w-300 {
  width: 300px;
}
.block {
  display: block;
}
.rounded-lg {
  border-radius: 8px;
}
.bg-gray-800 {
  --tw-bg-opacity: 1;
  background-color: rgb(31 41 55 / var(--tw-bg-opacity));
}
.w-16 {
  width: 64px;
}
.h-9 {
  height: 36px;
}
.bg-yellow-800 {
  --tw-bg-opacity: 1;
  background-color: rgb(133 77 14 / var(--tw-bg-opacity));
}
.mt-2 {
  margin-top: 8px;
}
.mb-4 {
  margin-bottom: 16px;
}
.rounded {
  border-radius: 4px;
}
.font-bold {
  font-weight: 700;
}
.mt-1 {
  margin-top: 4px;
}
.mb-1 {
  margin-bottom: 4px;
}
.hidden {
  display: none;
}
.hover\:green-700 {
  --tw-text-opacity: 1;
  color: rgb(21 128 61 / var(--tw-text-opacity));
}
.hover\:text-green-700:hover {
  --tw-text-opacity: 1;
  color: rgb(21 128 61 / var(--tw-text-opacity));
}
.ml-3 {
  margin-left: 12px;
}
.break-words {
  overflow-wrap: break-word;
}
.hover\:text-red-700 {
  --tw-text-opacity: 1;
  color: rgb(185 28 28 / var(--tw-text-opacity));
}
.ml-auto {
  margin-left: auto;
}
.line-through {
  text-decoration-line: line-through;
}
.mr-1 {
  margin-right: 8px;
}
input[type=text] {
  color: white;
}


@media only screen and (min-width: 550px){
.w-350{
  width: 500px;
}
.w-300{
  width: 450px;
}
.w-16{
  width: 80px;
}
.text-3xl {
  font-size: 40px;
  line-height: 46px;
}
}